import React, { Component } from 'react'
import '../App.css';
import { template } from '@babel/core';
var vis=[50];
                for(var i=0;i<50 ;i++){
                    vis[i]=[50];
                    for(var j=0;j<50;j++){
                        vis[i][j]=0;
                    }
                }
export class Main extends Component {
    constructor(props){
        super(props);
        this.state={
            box:[],
            started:false,
            firstClick:true,
            highScore:100000,
            flagCount:40,
            timer:0,
            lost:0,
            won:0,
            disable:false,
            length:0,
            gridClass:"grid",
            height:16,
            bombNo:40,
            width:16,
            difficulty:"Medium"
        }
        this.renderBox=this.renderBox.bind(this);
        this.checkWon=this.checkWon.bind(this);
    }

    handleRight(e,i,j){
        e.preventDefault();
        if(this.state.flagCount===0){
            return;
        }
        var temp=this.state.box;
        var tempFlag=this.state.flagCount;
        if(temp[i][j].flag===1){
            temp[i][j].flag=0;
            tempFlag++;
        }else{
            temp[i][j].flag=1;
            tempFlag--;
        }
        this.setState({box:temp});
        this.setState({flagCount:tempFlag});
    }
    renderBox(){
        let temp=[this.state.height];
        var k=0;
        for(var i=0;i<this.state.height ;i++){
            temp[i]=[this.state.width];
            for(var j=0;j<this.state.width;j++){
                temp[i][j]=({id:k,flag:0,bomb:0,num:0,visible:0,class:"box ",img:"",bombClicked:0});
                k++;
            }
        }
        this.setState({timer:0,box:[...temp] , started:true,firstClick:true,flagCount:this.state.bombNo,lost:0,won:0,disable:false});
        for(var i=0;i<this.state.height ;i++){
            vis[i]=[this.state.width];
            for(var j=0;j<this.state.width;j++){
                vis[i][j]=0;
            }
        }
    }
    componentDidMount(){
        let temp=[this.state.height];
        var k=0;
        for(var i=0;i<this.state.height ;i++){
            temp[i]=[this.state.width];
            for(var j=0;j<this.state.width;j++){
                temp[i][j]=({id:k,flag:0,bomb:0,num:0,visible:0,class:"box ",img:"",bombClicked:0});
                k++;
            }
        }
        this.timerFunction();
        this.setState({box:temp , started:true});
    }
    timerFunction=()=>{
        
            this.Interval= setInterval(()=>{
                if(this.state.firstClick==false && this.state.lost==0 && this.state.won==0){
                this.setState(prevState => ({
                     timer: prevState.timer +1
                })


                )
            }
            } ,1000);
        
    }
    handleClick(p,q){
        var tempArray=this.state.box;
        var tempFlag=this.state.flagCount;
        if(this.state.firstClick===true){
            for(var i=0;i<this.state.bombNo;i++){
                const rand=Math.floor(Math.random() * (this.state.height * this.state.width - 0 + 1)) + 0;
                for(var j=0;j<this.state.height;j++){
                    for(var k=0;k<this.state.width;k++){
                        if(tempArray[j][k].id===rand && (j!==p && q!==k)){
                            tempArray[j][k].bomb=1;
                            tempArray[j][k].num='*';
                        }
                    }
                }
            }
            for(var i=0;i<this.state.height;i++){
                var bombCount=0;
                for(var j=0;j<this.state.width;j++){
                    bombCount=0;
                    if(i-1>=0 && j-1>=0  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i-1][j-1].bomb===1);
                    }
                    if(i>=0 && j-1>=0  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i][j-1].bomb===1);
                    }
                    if(i+1<this.state.height && j-1>=0 && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i+1][j-1].bomb===1);
                    }
                    if(i-1>=0 && j>=0  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i-1][j].bomb===1);
                    }
                    if(i+1<this.state.height && j >=0  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i+1][j].bomb===1);
                    }
                    if(j+1<this.state.width && i-1>=0  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i-1][j+1].bomb===1);
                    }
                    if(j+1<this.state.width && i>=0  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i][j+1].bomb===1);
                    }
                    if(j+1<this.state.width && i+1<this.state.height  && tempArray[i][j].bomb===0){
                        bombCount=bombCount + (tempArray[i+1][j+1].bomb===1);
                    }
                    if(tempArray[i][j].num==='*' || bombCount===0){
                        continue;
                    }
                    
                    tempArray[i][j].num=bombCount;
                    if(bombCount===1){
                            tempArray[i][j].class+="one";
                            
                    }else if(bombCount===2){
                        tempArray[i][j].class+="two";
                    }else if(bombCount===3){
                        tempArray[i][j].class+="three";
                    }else if(bombCount===4){
                        tempArray[i][j].class+="four";
                    }else if(bombCount===5){
                        tempArray[i][j].class+="five";
                    }else if(bombCount===6){
                        tempArray[i][j].class+="six";
                    }else if(bombCount===7){
                        tempArray[i][j].class+="seven";
                    }else if(bombCount===8){
                        tempArray[i][j].class+="eight";
                    }
                }
            }
            if(this.state.box[p][q].bomb===1){
                if(tempArray[p][q].flag===1){
                    tempArray[p][q].flag=0;
                    tempFlag++;
                }
                alert("game over");
            }else if(this.state.box[p][q].num>0){
                tempArray[p][q].visible=1;
                if(tempArray[p][q].flag===1){
                    tempArray[p][q].flag=0;
                    tempFlag++;
                }
                vis[p][q]=1;
            }else  if(this.state.box[p][q].num===0){  
                
                vis[p][q]=1;
                var temp= new Array();
                temp.push([p,q]);
                while(temp.length!==0){
                    var h=temp.pop();
                    var  x=h[0];
                    var  y=h[1];
                    if(tempArray[x][y].flag===1){
                        tempArray[x][y].flag=0;
                        tempFlag++;
                    }
                    if(tempArray[x][y].num > 0 ){
                        
                        tempArray[x][y].visible=1;
                        continue;
                    }
                    
                    console.log(x,y);
                    vis[x][y]=1;
                    tempArray[x][y].visible=1;
                    if(x-1>=0 && y-1>=0  && tempArray[x-1][y-1].bomb===0 && vis[x-1][y-1]===0){
                        temp.push([x-1,y-1]);
                    }
                    if(x>=0 && y-1>=0  && tempArray[x][y-1].bomb===0 && vis[x][y-1]===0){
                       temp.push([x,y-1]);
                    }
                    if(x+1<this.state.height && y-1>=0 && tempArray[x+1][y-1].bomb===0 && vis[x+1][y-1]===0){
                       temp.push([x+1,y-1]);
                    }
                    if(x-1>=0 && y>=0  && tempArray[x-1][y].bomb===0 && vis[x-1][y]===0){
                       temp.push([x-1,y]);
                    }
                    if(x+1<this.state.height && y >=0  && tempArray[x+1][y].bomb===0 && vis[x+1][y]===0){
                      temp.push([x+1,y]);
                    }
                    if(y+1<this.state.width && x-1>=0  && tempArray[x-1][y+1].bomb===0 && vis[x-1][y+1]===0){
                        temp.push([x-1,y+1]);
                    }
                    if(y+1<this.state.width && x>=0  && tempArray[x][y+1].bomb===0 && vis[x][y+1]===0){
                        temp.push([x,y+1]);
                    }
                    if(y+1<this.state.width && x+1<this.state.height  && tempArray[x+1][y+1].bomb===0 && vis[x+1][y+1]===0){
                        temp.push([x+1,y+1]);
                    }
                }
            }
            this.setState({box:tempArray , firstClick:0 ,flagCount:tempFlag});
        }else{
            if(this.state.box[p][q].bomb===1){
                for(var i=0;i<this.state.height;i++){
                    for(var j=0;j<this.state.width;j++){
                        if(this.state.box[i][j].bomb===1){
                            tempArray[i][j].visible=1;
                            if(tempArray[p][q].flag===1){
                                tempArray[p][q].flag=0;
                                tempFlag++;
                            }
                        }
                    }
                }
                this.setState({lost:1});
                
                

                this.setState({disable:true});
                tempArray[p][q].bombClicked=1;

            }else if(this.state.box[p][q].num>0){
                tempArray[p][q].visible=1;
                if(tempArray[p][q].flag===1){
                    tempArray[p][q].flag=0;
                                tempFlag++;
                }
                vis[p][q]=1;
            }else if(this.state.box[p][q].num===0){  
                
                vis[p][q]=1;
                var temp= new Array();
                temp.push([p,q]);
                while(temp.length!==0){
                    var h=temp.pop();
                    var  x=h[0];
                    var  y=h[1];
                    if(tempArray[x][y].flag===1){
                        tempArray[x][y].flag=0;
                                tempFlag++;
                    }
                    if(tempArray[x][y].num > 0 ){
                        tempArray[x][y].visible=1;
                        continue;
                    }
                    
                    console.log(x,y);
                    vis[x][y]=1;
                    tempArray[x][y].visible=1;
                    if(x-1>=0 && y-1>=0  && tempArray[x-1][y-1].bomb===0 && vis[x-1][y-1]===0){
                        temp.push([x-1,y-1]);
                    }
                    if(x>=0 && y-1>=0  && tempArray[x][y-1].bomb===0 && vis[x][y-1]===0){
                       temp.push([x,y-1]);
                    }
                    if(x+1<this.state.height && y-1>=0 && tempArray[x+1][y-1].bomb===0 && vis[x+1][y-1]===0){
                       temp.push([x+1,y-1]);
                    }
                    if(x-1>=0 && y>=0  && tempArray[x-1][y].bomb===0 && vis[x-1][y]===0){
                       temp.push([x-1,y]);
                    }
                    if(x+1<this.state.height && y >=0  && tempArray[x+1][y].bomb===0 && vis[x+1][y]===0){
                      temp.push([x+1,y]);
                    }
                    if(y+1<this.state.width && x-1>=0  && tempArray[x-1][y+1].bomb===0 && vis[x-1][y+1]===0){
                        temp.push([x-1,y+1]);
                    }
                    if(y+1<this.state.width && x>=0  && tempArray[x][y+1].bomb===0 && vis[x][y+1]===0){
                        temp.push([x,y+1]);
                    }
                    if(y+1<this.state.width && x+1<this.state.height  && tempArray[x+1][y+1].bomb===0 && vis[x+1][y+1]===0){
                        temp.push([x+1,y+1]);
                    }
                }
            }
            this.setState({box:tempArray , firstClick:0 , flagCount:tempFlag});
            this.checkWon(tempArray);
        }
        
    }
    checkWon(tempArray){
        var win=1;
        for(var i=0;i<this.state.height;i++){
            for(var j=0;j<this.state.width;j++){
                if(tempArray[i][j].bomb===0 && tempArray[i][j].visible===0){
                    win=0;
                }
            }
        }
        if(win){
            var x= this.state.highScore;
            this.setState({disable:true});
            x= Math.min(x,this.state.timer);
            this.setState({highScore:x});
        }
        this.setState({won:win});
    }
    setDifficulty(i){
        if(i===1){
            this.setState({difficulty:"Easy" , height:8, width:8 ,flagCount:10, bombNo:10,gridClass:"grid grid-easy"},()=>this.renderBox());
        }else if(i===2){
            this.setState({difficulty:"Medium" , height:16, width:16,flagCount:40, bombNo:40, gridClass:"grid"},()=>this.renderBox());
        }else{
            this.setState({difficulty:"Hard" , height:16, width:30,flagCount:100,bombNo:100,gridClass:"grid grid-hard"}, ()=>this.renderBox());
        }
        
    }
    render() {
        return (

            <>
            <div className="header">Minesweeper! </div>
        
            {/* start btn */}
        <>{
            this.state.won || this.state.lost ?
            <div className="start-body">
            {
                this.state.lost===1?<div className="result">You Lost<div><img className="imgAnimation" src="./clock1.png"className="img-clock"/>-</div><div>High Score: {this.state.highScore===100000?"-" : this.state.highScore} </div></div> : <div className="result">You Won<div><img src="./clock1.png" width="30px"height="30px"/>{this.state.timer}</div><div>High Score: {this.state.highScore}</div></div>
            }
             <button onClick={()=>this.renderBox()} className="start-btn" >{"Try Again!"} </button> 
            </div>: null
        }</>
            <div className="grid-box" id="grid-box" disabled={this.state.disabled}>
                <div className="btn-grid">
                <div className="btn-flex">
                <div className="flag"><img src="./flag1.png" height="100" width="100"/>  { this.state.flagCount}</div>
                </div>
                <div className="btn-flex">
                <div className="timer"><img src="./clock1.png"/> {this.state.timer} </div>
                 </div>
                 <div className="btn-flex">
                  <div className ="dropdown">
                 <button className ="dropbtn">{this.state.difficulty}</button>
                 <div className ="dropdown-content">
                 <button onClick={()=>this.setDifficulty(1)}>Easy</button>
                 <button onClick={()=>this.setDifficulty(2)}>Medium</button>
                 <button onClick={()=>this.setDifficulty(3)}>Hard</button>
                 </div>
               </div>
               </div> 

                </div>

                <div className={this.state.gridClass}>
                {this.state.started===true?
                    this.state.box.map((x,i)=>(
                        x.map((y,j)=>(<>{this.state.box[i][j].visible===0?
                            <button onClick={()=>this.handleClick(i,j)} onContextMenu={(event)=>this.handleRight(event,i,j)} disabled={this.state.disable}><>{
                                this.state.box[i][j].flag===1? <img src="./flag1.png" width="30" height="30"/>:""
                                
                            }</></button>
                             : <div className={this.state.box[i][j].class} >{
                                <>
                                {
                                    this.state.box[i][j].bomb===1?<>{
                                        this.state.box[i][j].bombClicked===0?
                                    <img src="https://minesweeper.online/img/skins/hd/mine.svg?v=2" width="43" height="43"/>
                                : <img src="https://minesweeper.online/img/skins/hd/mine_red.svg?v=2" width="43" height="43"/>
                                }</>:<>{
                                        this.state.box[i][j].num===0?null:
                                        this.state.box[i][j].num
                                        
                                        }</>
                                }
                                
                                </>
                            }
                            </div>
                }</>)) )):null
                }
                </div>
            </div>
            </>
        )
    }
}

export default Main
